.special {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-size: 14px;
  color: #666;
}
.special--disabled {
  color: rgba(179, 179, 179, 0.5);
}

.weight {
  background: #1698D9;
  border-radius: 50%;
  width: 80px;
  height: 80px;
  text-align: center;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  color: #fff;
}
.weight__quantity {
  font-size: 42px;
  line-height: 22px;
  position: relative;
  top: 20px;
}
.weight__measure {
  font-size: 22px;
  position: absolute;
  bottom: 6px;
  left: 29px;
}
.weight--disabled {
  background: #B3B3B3;
}

.product {
  position: relative;
  width: 320px;
  min-height: 483px;
  background: transparent;
  cursor: pointer;
}
.product__content {
  min-height: 432px;
  padding: 0 48px;
  position: relative;
  z-index: 1;
  border: 4px solid #1698D9;
  border-top: none;
  border-radius: 0 0 10px 10px;
  background: #fff;
}
.product__content::after {
  content: "";
  background: url(/test-task-react-nyamushka/static/media/cat.284a8185.svg) no-repeat 82% 100%;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1;
}
.product__content--disabled::after {
  opacity: 0.5;
}
.product__content--selected {
  border-color: #E52E7A;
}
.product__content--disabled {
  border-color: #B3B3B3;
}
.product__description {
  min-height: 23px;
  padding: 16px 48px 0 4px;
  position: relative;
  z-index: 2;
  border-top: 4px solid #1698D9;
  border-right: 4px solid #1698D9;
  border-left: 1px solid #fff;
  border-radius: 0 10px 0 0;
  background: #fff;
  margin-bottom: -9px;
  width: 220px;
  right: -43px;
}
.product__description::after {
  content: "";
  display: block;
  position: absolute;
  top: -2px;
  left: -41px;
  height: 2px;
  width: 7px;
  border-bottom: 31px solid #fff;
  border-left: 36px solid transparent;
}
.product__description::before {
  content: "";
  display: block;
  position: absolute;
  top: -6px;
  left: -44px;
  height: 2px;
  width: 5px;
  border-bottom: 34px solid #1698D9;
  border-left: 39px solid transparent;
}
.product__description__content {
  position: relative;
  z-index: 2;
  padding-left: 44px;
  border-left: 4px solid #1698D9;
  border-right: 4px solid #1698D9;
  background: #fff;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-size: 16px;
  color: #666;
}
.product__description__content__text {
  position: relative;
  top: -12px;
}
.product__description__content__text--disabled {
  color: rgba(179, 179, 179, 0.5);
}
.product__description__content__text--selected {
  color: #E52E7A;
}
.product__description--selected,
.product__description__content--selected {
  border-color: #E52E7A;
}
.product__description--selected::before {
  border-bottom-color: #E52E7A;
}
.product__description--disabled,
.product__description__content--disabled {
  border-color: #B3B3B3;
}
.product__description--disabled::before {
  border-bottom-color: #B3B3B3;
}
.product__title {
  margin: -7px 0 0 -4px;
  font-size: 24px;
  font-weight: bold;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  word-wrap: break-word;
}
.product__title--disabled {
  color: rgba(179, 179, 179, 0.5);
}
.product__title--bold {
  position: relative;
  top: 2px;
  left: -2px;
  font-size: 48px;
}
.product__footer {
  position: absolute;
  width: 320px;
  left: 0px;
  top: 450px;
  text-align: center;
  color: #fff;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-size: 13px;
  pointer-events: none;
}
.product__footer__link {
  color: #1698D9;
  border-bottom: 1px dashed #1698D9;
  text-decoration: none;
  pointer-events: auto;
}
.product__footer__link:hover {
  border: none;
}
.product__footer__text--disabled {
  color: #FFFF66;
}
.product__weight {
  position: absolute;
  right: 12px;
  bottom: 14px;
}
.product__special {
  min-height: 51px;
  margin: 15px 0 286px -3px;
}
.product--disabled {
  cursor: default;
}

.products-list {
  width: 1120px;
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  -webkit-flex-wrap: wrap;
  flex-wrap: wrap;
  margin-bottom: 60px;
}
.products-list__header {
  margin: 0 auto 27px;
  text-align: center;
  width: 100%;
}
@media (max-width: 1119px) {
  .products-list {
    width: 720px;
  }
  .products-list__item:nth-child(3n) {
    margin-top: 70px;
  }
}
@media (max-width: 719px) {
  .products-list {
    width: 320px;
    min-width: 320px;
  }
  .products-list__item {
    margin-top: 70px;
  }
  .products-list__item:first-child {
    margin: 0;
  }
}

.header__title {
  margin: 0;
  font-family: 'Exo2.0-Thin', sans-serif;
  font-size: 36px;
  background-color: #666565;
  color: transparent;
  text-shadow: 0px 1px 3px rgba(255, 255, 255, 0.1);
  -webkit-background-clip: text;
  background-clip: text;
}

.app {
  background: url(/test-task-react-nyamushka/static/media/background.a3f1c08e.svg) no-repeat center center fixed;
  background-size: cover;
  height: 100%;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column;
  -webkit-justify-content: center;
  justify-content: center;
}

